import Vue from 'vue'
import aboutMe from '@/components/aboutMe.vue'
import myStudies from '@/components/myStudies.vue'
import myExperiences from '@/components/myExperiences.vue'
import myProjects from '@/components/myProjects.vue'
import contact from '@/components/contact.vue'
import blog from '@/components/blog.vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

export default new VueRouter({
    routes: [
        { path: '/', component: aboutMe },
        { path: '/studies', component: myStudies },
        { path: '/experiences', component: myExperiences },
        { path: '/myProjects', component: myProjects },
        { path: '/contact', component: contact },
        { path: '/blog', component: blog }
      ]
})